import { defineComponent, ref, onMounted } from '@nuxtjs/composition-api';
export default defineComponent({
    props: {
        groups: {
            type: Array,
            default: () => []
        },
        items: {
            type: Array,
            default: () => []
        },
        fullWidth: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const panel = ref([]);
        onMounted(() => {
            panel.value = props.groups
                .filter((group) => !group.collapse)
                .map((group) => props.groups.indexOf(group));
        });
        return { panel };
    }
});
