import { defineComponent, watch, ref } from '@nuxtjs/composition-api';
import Content from '../internal/context-menu/context-content.vue';
export default defineComponent({
    components: {
        Content
    },
    props: {
        groups: {
            type: Array,
            default: () => []
        },
        items: {
            type: Array,
            default: () => []
        },
        value: {
            type: Boolean,
            default: false
        },
        menuAttrs: {
            type: Object,
            default: () => { }
        },
        bottomSheetAttrs: {
            type: Object,
            default: () => { }
        }
    },
    setup(props) {
        const menu = ref(false);
        const on = {
            click: () => {
                menu.value = true;
            }
        };
        watch(() => props.value, () => (menu.value = props.value), { immediate: true });
        return { menu, on };
    }
});
